<!---->
<template>
  <body id="poster">
    <el-scrollbar style="height: 100%">
      <el-form
        ref="registerFormRef"
        :model="registerForm"
        :rules="registerFormRules"
        class="register-container"
        label-position="left"
        label-width="0px"
        v-loading="loading"
        size="medium"
      >
        <h3 class="login_title"></h3>
        <el-form-item prop="username">
          <el-input
            v-model="registerForm.username"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item prop="password">
          <el-input
            type="password"
            v-model="registerForm.password"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item prop="confirmPassword">
          <el-input
            type="password"
            v-model="registerForm.confirmPassword"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item prop="telephone">
          <el-input
            v-model="registerForm.telephone"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item prop="authCode">
          <el-input
            v-model="registerForm.authCode"
            placeholder=""
            class="authInput"
          ></el-input>
          <el-button class="authButton" v-on:click="getAuthCode"
            ></el-button
          >
        </el-form-item>

        <el-upload
          class="upload"
          action
          :drag="true"
          :multiple="true"
          :file-list="images"
          :http-request="uploadHttp"
          :before-upload="beforeAvatarUpload"
          :on-remove="handleRemove"
        >
          <i class="el-icon-plus avatar-uploader-icon"></i>
          <p id="img-context"></p>
          <div class="el-upload__tip" slot="tip">
            jpg/jpeg/png5MB
          </div>
        </el-upload>
        <el-form-item style="width: 100%">
          <el-button
            type="primary"
            style="width: 100%; background: #505458; border: none"
            v-on:click="register"
            ></el-button
          >
        </el-form-item>
      </el-form>
    </el-scrollbar>
  </body>
</template>

<script>
import ossClient from "../assets/config/aliyun.oss.client";
import api from "@/assets/config/api.js";
import axios from "axios";
import qs from "qs";
export default {
  name: "Upload",
  data() {
    let checkPassword = (rule, value, cb) => {
      const regPassword = /^\w{6,50}$/;
      if (regPassword.test(value)) {
        //
        return cb();
      }
      cb(new Error("6-15,"));
    };
    let checkConfirmPassword = (rule, value, cb) => {
      const regPassword = this.registerForm.password;
      if (regPassword === value) {
        //
        return cb();
      }
      cb(new Error("!"));
    };
    let checktelephone = (rule, value, cb) => {
      const regNumber = /^1(?:3\d|4[4-9]|5[0-35-9]|6[67]|7[013-8]|8\d|9\d)\d{8}$/;
      if (regNumber.test(value)) {
        //
        return cb();
      }
      cb(new Error("!"));
    };
    return {
      loading: true,
      //
      images: [],
      uploadConf: {
        region: null,
        accessKeyId: null,
        accessKeySecret: null,
        bucket: null,
      },
      //
      registerForm: {
        username: "",
        password: "",
        confirmPassword: "",
        telephone: "",
        authCode: "",
        icon: "",
        // imgUrl: "https://tongji4m3.oss-cn-beijing.aliyuncs.com/OIP.jpg",
      },
      //
      registerFormRules: {
        //    
        username: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 3,
            max: 10,
            message: "3-10",
            trigger: "blur",
          },
        ],
        //    
        password: [
          { required: true, message: "", trigger: "blur" },
          { validator: checkPassword, trigger: "blur" },
        ],
        confirmPassword: [
          { required: true, message: "", trigger: "blur" },
          { validator: checkConfirmPassword, trigger: "blur" },
        ],
        //    
        telephone: [
          { required: true, message: "", trigger: "blur" },
          { validator: checktelephone, trigger: "blur" },
        ],
        //    
        authCode: [{ required: true, message: "", trigger: "blur" }],
      },
    };
  },
  //
  created() {
    //
    let _this = this;
    setTimeout(() => {
      this.loading = false;
    }, 500);
    document.onkeydown = function (e) {
      let key = window.event.keyCode;

      if (key === 13) {
        _this.register(); //
      }
    };
  },
  methods: {
    async register() {
      console.log(this.$api.registerUrl);
      //
      this.$refs.registerFormRef.validate((valid) => {
        if (!valid) return;
      });

      try {
        const res = await axios.post(
          this.$api.registerUrl,
          JSON.stringify({
            username: this.registerForm.username,
            password: this.registerForm.password,
            telephone: this.registerForm.telephone,
            authCode: this.registerForm.authCode,
            icon: this.registerForm.icon,
          })
        );

        console.log(res);
        if (res.data.code == 200) {
          this.$message.success("");

          this.$router.push("/login");
        } else {
          this.$message.error("");
        }
      } catch (err) {
        console.log(err);
        this.$message.error("");
      }
    },

    async getAuthCode() {
      console.log(this.registerForm.telephone);
      console.log(this.$api.getAuthCodeUrl);
      try {
        const res = await axios.post(
          this.$api.getAuthCodeUrl,
          this.registerForm.telephone
        );

        console.log(res);
        if (res.data.code == 200) {
          this.$message.success("");
        }
      } catch (err) {
        console.log(err);
        this.$message.error("");
      }
    },

    /**
     * 
     */
    async init() {
      //token  
      this.uploadConf.region = "oss-cn-shanghai";
      this.uploadConf.accessKeyId = "LTAI4FzMDhgBN9LMBr71T3Ny";
      this.uploadConf.accessKeySecret = "hTPgQQSyBgEDnfMNe06RPf8ecDafpz";
      this.uploadConf.bucket = "tongji-boying";
    },
    /**
     * OSS
     */
    uploadHttp({ file }) {
      this.init();
      const { imgName } = "ALIOSS_IMG_";
      const fileName = `${imgName}/${Date.parse(new Date())}`; //
      ossClient(this.uploadConf)
        .put(fileName, file, {
          ContentType: "image/jpeg",
        })
        .then(({ res, url, name }) => {
          if (res && res.status === 200) {
            console.log(`OSS`, res, url, name);
            console.log(url);
            this.registerForm.icon = url;
          }
        })
        .catch((err) => {
          console.log(`OSS`, err);
        });
    },

    /**
     * 
     */
    beforeAvatarUpload(file) {
      const isJPEG = file.name.split(".")[1] === "jpeg";
      const isJPG = file.name.split(".")[1] === "jpg";
      const isPNG = file.name.split(".")[1] === "png";
      const isLt500K = file.size / 1024 / 1024 / 5 < 2;
      if (!isJPG && !isJPEG && !isPNG) {
        this.$message.error(" JPEG/JPG/PNG !");
      }
      if (!isLt500K) {
        this.$message.error(" 5MB!");
      }
      return (isJPEG || isJPG || isPNG) && isLt500K;
    },

    /**
     * 
     */
    handleRemove(file, fileList) {
      console.log(``, fileList);
    },
  },
};
</script>
<style scoped>
#poster {
  height: 100%;
  width: 100%;
  background-size: cover;
  position: fixed;
  padding: 0;
  margin: 0;
}

body {
  color: rgba(255, 255, 255, 0.65);
  background-color: #24292e;
  /*background-image: url(../../assets/img/star-bg.svg),*/
  /*linear-gradient(#191c20, #24292e 15%);*/
  background-repeat: repeat-x;
  background-position: center 0, 0 0, 0 0;
  margin-left: 0;
  margin-top: 1;
}

.register-container {
  border-radius: 15px;
  background-clip: padding-box;
  margin: 10px auto;
  width: 350px;
  padding: 35px 35px 15px 35px;
  background: #fff;
  border: 1px solid #eaeaea;
}

.login_title {
  letter-spacing: 10px;
  margin: -30px auto 10px auto;
  text-align: center;
  color: #505458;
}

#img-context {
  text-align: center;
  font-size: 17px;
  color: #b0b0b0;
  margin-top: 50px;
}

.el-upload__tip {
  text-align: center;
  font-size: 8px;
  color: rgba(52, 52, 52, 0.7);
}

/*  */
.authInput {
  width: 55%;
}

.authButton {
  margin-left: 45px;
}
</style>
